a {
    text-decoration: none;
}
/*= input focus effects css
=========================== */

:focus {
    outline: none;
}
.form-group {    
    width: 100%;
    margin: 30px 0;
    position: relative;
    z-index: 3;
}
/* necessary to give position: relative to parent. */

input[type="text"] {
	font: {
	    family: $font-family-title;
	    size: 16px;
		weight: $font-weight-light;
	}
	color: $mosque;
    width: 100%;
    box-sizing: border-box;
    letter-spacing: 1px;
}
.effect-20 {
    border: 1px solid #fff;
    padding: 10px 14px;
    transition: 0.4s;
    background: transparent;
}
.effect-20 ~ .focus-border:before,
.effect-20 ~ .focus-border:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #0f7986;
    transition: 0.3s;
}
.effect-20 ~ .focus-border:after {
    top: auto;
    bottom: 0;
    left: auto;
    right: 0;
}
.effect-20 ~ .focus-border i:before,
.effect-20 ~ .focus-border i:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 2px;
    height: 0;
    background-color: #0f7986;
    transition: 0.4s;
}
.effect-20 ~ .focus-border i:after {
    left: auto;
    right: 0;
    top: auto;
    bottom: 0;
}
.effect-20:focus ~ .focus-border:before,
.effect-20:focus ~ .focus-border:after,
.has-content.effect-20 ~ .focus-border:before,
.has-content.effect-20 ~ .focus-border:after {
    width: 100%;
    transition: 0.3s;
}
.effect-20:focus ~ .focus-border i:before,
.effect-20:focus ~ .focus-border i:after,
.has-content.effect-20 ~ .focus-border i:before,
.has-content.effect-20 ~ .focus-border i:after {
    height: 100%;
    transition: 0.4s;
}
.effect-20 ~ label {
    position: absolute;
    left: 14px;
	font: {
	    family: $font-family-title;
	    size: 16px;
		weight: $font-weight-light;
	}
	text-transform: uppercase;
	color: $white;   
    width: 100%;
    top: 15px;    
    transition: 0.3s;
    z-index: -1;
    letter-spacing: 0.5px;
}
.effect-20:focus ~ label,
.has-content.effect-20 ~ label {
    top: -18px;
    left: 0;
    font-size: 12px;
    color: $mosque;
    transition: 0.3s;
}
textarea{
	width: 100%;
	height: 250px;	
	resize: none;
	font: {
	    family: $font-family-title;
	    size: 16px;
		weight: $font-weight-light;
	}
	color: $white;    
    box-sizing: border-box;
    letter-spacing: 1px;	

    &:focus {
    	color: $mosque;
    }
    &.has-content{
    	&.effect-20 {
    		textarea{
    			color: $mosque;
    		}
    	}
    }
}