header {
  width: 100%;

  &.sticky-header {
    position: fixed;
    z-index: 200;

    .global-wrapper-menu {
      width: 300px;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100%;

      .trait {
        width: 1px;
        height: 20px;
        background: $white;
      }

      .bg-logo {
        width: 175px;
        padding: 10px;
        background: $white;

        .logo {
          display: block;
          background: url('/images/worklift_logo.png') no-repeat;
          width: 150px;
          height: 45px;
          background-size: contain;
          background-position: 0 50%;
          margin: 0 auto;
        }
      }
    }
  }
}
